.app__container {
  margin: 0 auto;
}

.main__container {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .main__container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .main__container {
    max-width: 1280px;
    padding: 0 40px;
  }
}

@media screen and (min-width: 1440px) {
  .main__container {
    max-width: 1440px;
    padding: 0 120px;
  }
}

@media screen and (min-width: 1920px) {
  .main__container {
    max-width: 1920px;
    padding: 0 240px;
  }
}
