.search__input {
  font-size: 16px;
  line-height: calc(20 / 16);
  padding: 5px 20px;
  border-radius: 16px;
  border: 1px solid transparent;
}

.search__input:is(:hover, :focus) {
  background-color: rgb(228, 242, 252);
  border: 1px solid rgb(164, 194, 218);
}

@media screen and (min-width: 768px) {
  .search__input {
    font-size: 18px;
    line-height: calc(24 / 18);
    padding: 10px 20px;
  }
}
